import React from 'react'
import image from '../../Assets/Images/avataaars.svg'
import style from './Home.module.css'

export default function Home() {
  return <>
  <div className={`container-fluid p-5 ${style.containerBg}`}>
    <div className="container p-5  d-flex justify-content-center">
   <div className="roww p-4 my-5  text-center text-white">
   <img className={`${style.imageWidth} text-center mb-4 `} src={image} alt="" />
    <h2 className={` fw-bold fs-1 mb-3`}>START FRAMEWORK</h2>
    <div className={`${style.starIcon} mb-3 `}>
       <i className='fas fa-star fa-xl position-relative '></i>
        <div className={`${style.leftBar} `}></div>
        <div className={`${style.rightBar}`}></div>
    </div>
    <p className='mb-1'>
    Graphic Artist - Web Designer - Illustrator
    </p>
   </div>
    </div>
  </div>
  </>
}
