import style from './Contact.module.css'

export default function Contact() {

  return <>

  <div className="container  p-5">
    <div className="row m-5 p-5">
        <div className="col-md-12 p-md-5">
        <div className={`userName mb-3 `} >
        <p className={`m-0 p-2  ${style.label} `}>User Name:</p>
        <input type="text" placeholder='userName' className={`form-control ${style.formControl} `}/>
        </div>
        <div className={`userage mb-3  `} >
        <p className={`m-0 p-2  ${style.label} `}>User Age:</p>
        <input type="number" placeholder='userAge' className={`form-control ${style.formControl} `}/>
        </div>
        <div className={`useremail  mb-3 `} >
        <p className={`m-0 p-2  ${style.label} `}>User Email:</p>
        <input type="email" placeholder='userEmail' className={`form-control ${style.formControl} `}/>
        </div>
        <div className={`userpass mb-1 `} >
        <p className={`m-0 p-2  ${style.label} `}>User Password :</p>
        <input type="password" placeholder='userPassword' className={`form-control ${style.formControl} `}/>
        </div>
        <button className={`btn ${style.btnMain}  mt-4`}>Submit</button>
        </div>
    </div>
  </div>
  
  
  </>
}
