import React from 'react'
// import  { useState, useEffect } from 'react';
import style from './NavBar.module.css'
import { Link } from 'react-router-dom';


export default function NavBar() {

  return <>
  {
    // Edit NavBar while scrolling
    window.addEventListener('scroll',function(){
      let navbar = this.document.querySelector('.navbar');
      navbar.classList.replace(`${style.navbarr}`,`${style.navbarrr}`);
    })

  }
  <nav className={` ${style.bgmain} navbar navbar-expand-lg ${style.navbarr} fixed-top navbar-dark`}>
  <div className="container">
    <Link to={""} className='navbar-brand fs-2 fw-bold'>START FRAMEWORK</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className={`nav-item  px-2 fw-bold`}>
          <Link to={'about'} className={`nav-link ${style.navItemWhite}`}>ABOUT</Link>
        </li>
        <li className={`nav-item  px-2 fw-bold`}>
          <Link to={'protofolio'} className={`nav-link ${style.navItemWhite}`} >PROTOFOLIO</Link>
        </li>
        <li className={`nav-item  px-2 fw-bold`}>
          <Link to={'contact'} className={`nav-link ${style.navItemWhite}`} >CONTACT</Link>
        </li>
      </ul>

    </div>
  </div>
</nav>


  </>
}
